@font-face {
  font-family: "YS Text";
  font-style: normal;
  font-weight: 400;
  src: url("../resources/fonts/text-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "YS Text";
  font-style: normal;
  font-weight: 500;
  src: url("../resources/fonts/text-medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "YS Text";
  font-style: normal;
  font-weight: 900;
  src: url("../resources/fonts/text-black.woff") format("woff");
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.container {
  max-width: var(--container-width);
  padding: 0 var(--container-offset);
  margin: 0 auto;
}

.btn {
  display: block;
  padding: 24px 48px;
  border-radius: 36px;
  background-color: #FFDE40;
  box-shadow: 0px 4px 98px 0px rgba(255, 222, 64, 0.5882352941);
  font-family: "YS Text", sans-serif;
  font-size: 24px;
  font-weight: 500;
  line-height: 22px;
  color: var(--color-main);
}

:root {
  --font-family: "Roboto", sans-serif;
  --content-width: 1080px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --color-primary: #e3e3e3;
  --color-main: #21201F;
  --grid-gap: 30px;
}

.header {
  position: relative;
  background: linear-gradient(90deg, #FF5C4D 0%, #EB469F 25%, #8341EF 72%, #3F68F9 100%);
}
.header::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%);
}
.header::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.2));
}
.header-wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding: 124px 0;
  z-index: 1;
}
.header-info {
  max-width: 410px;
  width: 100%;
  z-index: 1;
}
.header-info__title {
  margin: 0;
  margin-bottom: 48px;
  font-family: "YS Text", sans-serif;
  font-size: 72px;
  font-weight: 900;
  line-height: 72px;
  letter-spacing: -0.02em;
  color: var(--color-main);
}
.header-info__descr {
  margin: 0;
  margin-bottom: 44px;
  font-family: "YS Text", sans-serif;
  font-size: 24px;
  font-weight: 500;
  line-height: 31px;
  color: var(--color-main);
}
.header-info__price {
  position: relative;
  display: inline-block;
  padding: 0 12px 0px 0px;
  background: radial-gradient(100% 100% at 100% 0%, #E0B8FF 0%, #B6B9FF 100%);
  border-radius: 50px;
}
.header-info__price::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.63), rgba(255, 255, 255, 0.63));
  border-radius: 50px;
}
.header-info__price_now {
  position: relative;
  display: inline-block;
  width: 79px;
  height: 33px;
  background: radial-gradient(100% 100% at 100% 0%, #E0B8FF 0%, #B6B9FF 100%);
  border-radius: 50px;
  text-align: center;
}
.header-info__price_old {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  font-size: 16px;
  color: #808080;
}
.header-logo {
  display: block;
  margin-bottom: 72px;
}
.header-image {
  position: absolute;
  top: 40px;
  left: 250px;
}
.header-image::before {
  content: "";
  bottom: 160px;
  left: 230px;
  width: 209px;
  height: 208px;
  position: absolute;
  background-image: url(../../resources/header-img-text.svg);
}

.advantages-title {
  margin: 0;
  margin-bottom: 48px;
  font-family: "YS Text", sans-serif;
  font-size: 56px;
  font-weight: 900;
  line-height: 56px;
  letter-spacing: -0.02em;
  color: var(--color-main);
}
.advantages-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.advantages-item {
  width: 400px;
}
.advantages-item:nth-child(2n) {
  margin-bottom: 60px;
}
.advantages-item:last-child {
  margin-bottom: 0;
}
.advantages-item__icon {
  margin-bottom: 16px;
}
.advantages-item__descr {
  margin: 0;
  font-family: "YS Text", sans-serif;
  font-size: 24px;
  font-weight: 500;
  line-height: 29px;
  letter-spacing: 0px;
  color: var(--color-main);
}

.yplus {
  margin-top: 90px;
}
.yplus-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.yplus-info {
  max-width: 420px;
  width: 100%;
}
.yplus-info__descr {
  margin: 0;
  font-family: "YS Text", sans-serif;
  font-size: 24px;
  font-weight: 500;
  line-height: 29px;
  letter-spacing: 0px;
  color: var(--color-main);
}
.yplus-title {
  font-family: "YS Text", sans-serif;
  font-size: 48px;
  font-weight: 900;
  line-height: 48px;
  letter-spacing: -0.02em;
  color: var(--color-main);
}

.subscription-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.subscription-image {
  position: relative;
}
.subscription-image::before {
  content: "";
  position: absolute;
  top: 140px;
  left: 40px;
  width: 412px;
  height: 369px;
  background-color: rgba(130, 65, 239, 0.29);
  border-radius: 50%;
  filter: blur(80px);
  transform: rotate(220deg);
}
.subscription-info {
  max-width: 410px;
  width: 100%;
}
.subscription-info__descr {
  margin: 0;
  margin-bottom: 48px;
  font-family: "YS Text", sans-serif;
  font-size: 24px;
  font-weight: 500;
  line-height: 29px;
  color: var(--color-main);
}
.subscription-title {
  margin: 0;
  margin-bottom: 48px;
  font-family: "YS Text", sans-serif;
  font-size: 48px;
  font-weight: 900;
  line-height: 48px;
  letter-spacing: -0.02em;
  color: var(--color-main);
}

.footer {
  padding: 110px 0 126px;
}
.footer-wrapper {
  display: flex;
  justify-content: center;
}
.footer-descr {
  max-width: 910px;
  width: 100%;
  font-family: "YS Text", sans-serif;
  font-size: 13px;
  font-weight: 400;
  line-height: 16px;
  color: #C5C5C5;
}